.config-summary{
  display: flex;
  flex-direction: column;
    text-align: center;
    background-color: transparent;
    padding: 0;
    margin-top: 10px;
  }
  .config-summary h3{
    padding: 0;
  }
  
  .config-list {
    display: flex;
    padding: 5px;
    justify-content: center;
  }
  
  .config-item{
    display: flex;
    text-wrap: nowrap;
    gap: 5px;
    align-items: center;
  }
  .config-edit-input{
    width: 30%;
    height: 1.3rem;
  }

  .config-item p{
    color: rgb(56, 240, 240);
    font-size: 1.5rem;
    margin: 0;
  }

  .config-list-icon{
    width: 50px;
    height: 50px;
    align-self: center;
  }

  .config-list-limitations{
    margin: 10px 0px 10px 0px;
  
  }
  
  .limitation-item{
    background-color: rgba(240, 255, 255, 0.315);
    border-bottom: 4px double white;
  }

  .edit-button{
      width: 35%;
      align-self: flex-end;
      font-size: 1.2rem;
      background-color: rgba(0, 255, 255, 0.432);
      border: 2px solid aliceblue;
  }
  .edit-button:hover{
    transform: scale(1.08);
    background-color: aliceblue;
    transition: transform 0.6s, background-color 0.6s;
}